html {
  scroll-behavior: smooth;
}

#nav-menu {
  top: 0px;
  position: sticky;
  z-index: 100;
}
.navbar {
  line-height: 45px;
}

#nav-menu > a:hover {
  color: #f56565;
  text-decoration: underline;
}

#img {
  height: 300px;
  width: 200px;
  border-radius: 30px;
  display: block;
  margin: auto;
}
.title {
  font-size: 22px;
  margin: 10px 0px;
  font-weight: 600;
}
.des {
  font-size: 16px;
  font-weight: 500;
  color: #718079;
}
.two {
  border: 1px solid goldenrod;
  /* margin: 10px 0px; */
  margin-right: 10px;
  margin-bottom: 10px;
  padding: 2px 10px;
  border-radius: 6px;
}

.rest {
  border: 1px solid teal;
  /* margin: 10px 0px; */
  margin-bottom: 10px;
  margin-right: 10px;
  padding: 2px 10px;
  border-radius: 6px;
}
.github {
  border: 1px solid teal;
  padding: 2px 10px;
  border-radius: 6px;
  margin-right: 10px;
  cursor: pointer;
}
.live {
  border: 1px solid teal;
  margin-right: 10px;
  padding: 2px 10px;
  border-radius: 6px;
  cursor: pointer;
}

.link {
  font-size: 13px;
  margin-left: 5px;
}
.box {
  padding-left: 10px;
  position: relative;
  top: -8px;
}
.imgskill {
  height: 27px;
  width: 27px;
}
.skillitem {
  /* border-bottom: 1px solid #718079; */
  width: 135px;
  /* width: 100%; */
  padding: 10px 0px;
}
.bug {
  border-bottom: 1px solid #718079;
  /* width: 135px; */
  /* width: 85%; */
  padding: 4.8px 0px;
}
.contactfirst {
  margin: 10px 0px;
}
.contactpad {
  margin-left: 10px;
  display: inline-block;
  position: relative;
  top: -8px;
}
.label {
  margin: 5px 0px;
}
.contactbtn {
  margin: 5px 0px;
}

.form {
  background-color: #2d3748;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  border-radius: 10px;
}
